<template>
  <div class="deal_body col-lg-9 col-xs-12 col-sm-12 reset-col">
    <el-alert style="background-color:#475669; color: #fff; margin-bottom:5px; padding-top:5px; padding-bottom:5px;" title="Offers on this page are streamed straight to you from our top stores." type="Black" @close="onBargainAlertClose">
    </el-alert>
    <span id="top-link-block hidden-md-down" class="text-center btn-fixed" :style="{'left': this.getOffset + 10 + 'px', 'bottom': '10px', 'display': (this.isScrolled) ? 'block' : 'none'}">
              <a href="" class="btn btn-secondary btn-sm btn-top" onclick="$('html,body').animate({scrollTop:0},'slow');return false;">
                  <i class="fa fa-arrow-up" aria-hidden="true"></i>
                  <p class="reset-col">Back To Top</p>
              </a>
          </span>
    <div class="feature-card col-lg-12" style="padding:0; margin:0 0 5px 0; z-index: 999;">
      <nav class="navbar navbar-inverse dynamic-nav" data-toggle="sticky-onscroll"
           style="background-color: #fff; padding:0; box-shadow: 1px 1px 5px #ccc; padding-top:5px; padding-bottom:5px;">
        <div style="width:100%; vertical-align: middle;">
          <div class="clearfix" style="padding-left:0; vertical-align: middle; padding-right:15px;  padding-left:15px;">
            <div class="d-inline-block dynamic-search col-lg-3 pull-left reset-col">
              <div class="input-group has-success">
                <el-input
                  icon="search"
                  placeholder="Enter keywords"
                  @change="onFilterChange"
                  v-model="filterInput"></el-input>
              </div>
            </div>
            <div class="d-inline-block pull-left price-range-filter hidden-sm-down" style="padding-left: 0px; padding-right:0;">
              <div class="row">
                <div class="col-lg-12 reset-col">
                  <div class="d-inline-block price-input" style="width:60px;">
                    <el-input
                      size="small"
                      placeholder="Min"
                      @change="onMinPriceChange"
                      v-model="minPrice">
                      <template slot="prepend">$</template>
                    </el-input>
                  </div>

                  <div class="d-inline-block price-input" style="width:60px; margin-left:7px;">
                    <el-input

                      size="small"
                      placeholder="Max"
                      @change="onMaxPriceChange"
                      v-model="maxPrice">
                      <template slot="prepend">$</template>
                    </el-input>
                  </div>
                </div>
              </div>
            </div>

            <div class="d-inline-block v-middle hidden-sm-down" style="width: 9.5em; margin-left: 10px;">
              <el-select v-model="checkedBargainType" @change="getBargainField" placeholder="Select">
                <el-option
                  v-for="item in bargainType"
                  :label="item.name"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>

            <div class="d-inline-block price-range-filter hidden-sm-down hidden-cm-tool" style="margin-left: 5px; padding-right:0; width:125px;">
              <div class="row">
                <div class="col-lg-12 reset-col" style="padding:0">
                  <div class="d-inline-block v-middle" style="width:100%; margin-left:15px; max-width:250px; margin-top:-5px;">
                    <span style="margin-top:0; padding:0; font-size:11px;"><b>Min Discount: {{discountChange}}%</b></span>
                    <el-slider v-model="discountChange" @change="onDiscountChange" style="margin-top:8px;"></el-slider>
                  </div>
                </div>
              </div>
            </div>

            <div class="d-inline-block price-range-filter hidden-sm-down hidden-cm-tool" style="margin-left: 30px; padding-right:0; width:125px;">
              <div class="row">
                <div class="col-lg-12 reset-col" style="padding:0">
                  <div class="d-inline-block v-middle" style="width:100%; max-width:250px; margin-top:-5px;">
                    <span style="margin-top:0; padding:0; font-size:11px;"><b>Min Rating: {{ratingChange}}</b></span>
                    <el-slider v-model="ratingChange" @change="onRatingChange" :step="0.1" :max="5" style="margin-top:8px;"></el-slider>
                  </div>
                </div>
              </div>
            </div>

            <div class="d-inline-block btn-group price-range-filter hidden-md-up" style="padding-left: 0px; padding-right:0;">
              <div class="dropdown btn category-dropdown category-hover" style="margin: 0; padding: 0; font-size: 1em;">
                <button class="btn btn-raised dropdown-toggle btn-secondary categories-list" data-toggle="dropdown" style="font-size:14px; padding-top:9px; padding-bottom:8px;">Advanced Filter</button>
                <div class="dropdown-menu category-dropdown" style="min-width:16rem; z-index:9999;">
                  <div class="row">
                    <h6 class="col-lg-12" style="font-size:12px; margin-top:5px; margin-bottom:0;"><b>Price Range:</b></h6>
                    <div class="col-lg-12 ad-filter-pad-top">
                      <div class="d-inline-block price-input" style="width:50px; margin-top:3px;">
                        <el-input
                          size="small"
                          placeholder="Min"
                          @change="onMinPriceChange"
                          v-model="minPrice">
                          <template slot="prepend">$</template>
                        </el-input>
                      </div>

                      <div class="d-inline-block price-input" style="width:50px; margin-left:7px; margin-top:3px;">
                        <el-input
                          size="small"
                          placeholder="Max"
                          @change="onMaxPriceChange"
                          v-model="maxPrice">
                          <template slot="prepend">$</template>
                        </el-input>
                      </div>
                    </div>
                  </div>
                  <div class="row ad-filter-pad-top">
                    <h6 class="col-lg-12" style="font-size:12px; margin-top:5px; margin-bottom:0;"><b>Min Discount: {{discountHiddenChange}}%</b></h6>
                    <div class="col-lg-12">
                      <div class="d-inline-block v-middle" style="width:100%; margin-left:5px; max-width:250px;">
                        <el-slider v-model="discountHiddenChange" @change="onDiscountHiddenChange"></el-slider>
                      </div>
                    </div>
                  </div>
                  <div class="row ad-filter-pad-top">
                    <h6 class="col-lg-12" style="font-size:12px; margin-top:5px; margin-bottom:0;"><b>Min Rating: {{ratingHiddenChange}}</b></h6>
                    <div class="col-lg-12">
                      <div class="d-inline-block v-middle" style="width:100%; margin-left:5px; max-width:250px;">
                        <el-slider v-model="ratingHiddenChange" @change="onRatingHiddenChange" :step="0.1" :max="5"></el-slider>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="dropdown btn category-hover" style="margin: 0; padding: 0; font-size: 1em;">
                <button class="btn btn-raised dropdown-toggle btn-secondary categories-list" data-toggle="dropdown" style="font-size:14px; padding-top:9px; padding-bottom:8px;">Store Filter</button>
                <div class="dropdown-menu category-dropdown" style="min-width:13rem; z-index:9999;">
                  <div class="row">
                    <div class="pure-radiobutton dropdown-item" v-for="field in bargainType">
                      <input type="radio" v-model="checkedBargainType" name="checkbox" :id="field.value" :value="field.value">
                      <label :for="field.value" @click="getBargainType(field)" class="reset-col" style="font-size:12px;">{{field.name}}</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="d-inline-block" style="padding-left: 0px; padding-left: 10px; vertical-align: middle; margin-top:4px;">
              <div class="dropdown category-hover">
                <el-tooltip :visible-arrow="false" popper-class="tooltip-border" content="Refresh" effect="light" :openDelay="700" placement="bottom">
                  <i class="fa fa-refresh" @click="refresh" style="font-size:18px; color:#039BE5; cursor: pointer;" aria-hidden="true"></i>
                </el-tooltip>
              </div>
            </div>

            <div class="d-inline-block pull-right" style="font-size: 1.2em;">
              <span style="color:#a0a0a0; font-size: 1.55em; cursor:pointer;" v-on:click="toggleLayout('grid')"
                    v-bind:class="{grid: this.toggle === 'grid'}"><i class="fa fa-th" aria-hidden="true"></i></span>
              <span style="color:#a0a0a0; font-size: 1.55em; padding-left:10px; cursor:pointer;" v-on:click="toggleLayout('list')"
                    v-bind:class="{grid: this.toggle === 'list'}"><i class="fa fa-th-list" aria-hidden="true"></i></span>
            </div>

          </div>
        </div>
      </nav>
    </div>
    <router-view :param="params" @page="pageUpdate"></router-view>
  </div>
</template>
<style>
  i.el-alert__closebtn {
    color: #fff;
    top: 10px;
    font-size:10px;
  }
  .price-input .el-input--small .el-input__inner {
    height: 35px !important;
  }
  .btn-top {
    border-color: #2196F3;
    background-color:#1F2D3D;
    padding-left:5px;
    padding-right:5px;
    color:white;
  }
  .btn-fixed {
    z-index: 10;
    position:fixed;
  }
</style>
<script>
  export default {
    data () {
      return {
        bargainAlert:  localStorage.getItem('bargainAlert') !== 'false',
        checkedCategory: [],
        checkedBargainType: 'all',
        toggle: 'grid',
        priceRange: [0, 10000],
        minPrice: null,
        max: 10000,
        minRating: 0,
        ratingChange: 0,
        ratingHiddenChange: 0,
        discountChange: 0,
        discountHiddenChange: 0,
        maxPrice: null,
        minDiscount: 0,
        offsetWidth: 0,
        scrolled: false,
        filterInput: '',
        bargainType: [
          {
            name: 'All',
            value: 'all'
          },
          {
            name: 'Ebay',
            value: 'ebay'
          },
          {
            name: 'Amazon',
            value: 'amazon'
          },
          {
            name: 'Best Buy',
            value: 'best-buy'
          },
          {
            name: 'Walmart',
            value: 'walmart'
          }
        ],
        params: {
          keywords: '',
          page: 1,
          minSalePrice: 0,
          maxSalePrice: 0,
          minRating: 0,
          minDiscount: 0,
          storeSlug: 'all',
        },
        category: [],
        storeList: [],
        deals: [],
      }
    },
    methods: {
      onRatingChange (value) {
        this.params.minRating = value
        this.ratingHiddenChange = value
      },
      onRatingHiddenChange (value) {
        this.params.minRating = value
        this.ratingChange = value
      },
      onDiscountChange (value) {
        this.params.minDiscount = value
        this.discountHiddenChange = value
      },
      onDiscountHiddenChange (value) {
        this.params.minDiscount = value
        this.discountChange = value
      },
      onBargainAlertClose () {
        localStorage.setItem('bargainAlert', false)
      },
      refresh () {
        this.$root.$emit('dealRefresh')
      },
      pageUpdate (page) {
        this.params.page = page
      },
      onFilterChange (input) {
        this.params.keywords = input
      },
      onPriceChange (price) {
        this.minPrice = price[0]
        this.maxPrice = price[1]
        this.params.minSalePrice = price[0]
        this.params.maxSalePrice = price[1]
      },
      onMinPriceChange (price) {
        price = parseInt(price)
        if (price) {
          if (price >= 0 && price <= this.priceRange[1]) {
            this.priceRange = [price, this.priceRange[1]]
            this.params.minSalePrice = price
          }
        } else {
          this.params.minSalePrice = null
        }
      },
      onMaxPriceChange (price) {
        price = parseInt(price)
        if (price) {
          if (price <= 10000 && price >= this.priceRange[0]) {
            this.priceRange = [this.priceRange[0], price]
            this.params.maxSalePrice = price
          }
        } else {
          this.params.maxSalePrice = null
        }
      },
      onPriceInputChange (price) {
        price = parseInt(price)
        if (price) {
          this.priceRange = price
        }
        this.params.minSalePrice = price
      },
      sortBy: function (name) {
        this.selected = name
      },
      toggleLayout: function(value) {
        window.localStorage.setItem('layout', value)
        this.toggle = value
      },
      setActive: function(value) {
        if (value != this.status) {
          this.status = value;
        }
      },
      getBargainType (field) {
        this.params.storeSlug = field.value
      },
      getBargainField (field) {
        this.params.storeSlug = field
      },
      handleScroll () {
        this.scrolled = window.scrollY > 100
      },
      handleResize () {
        this.offsetWidth = $('.deal_body').outerWidth() +  $('.deal_body').offset().left
      }
    },
    computed: {
      isScrolled: function() {
        if (this.scrolled) {
          if (window.scrollY > 100) {
            return true
          } else {
            return false
          }
        } else {
          return false
        }
      },
      getOffset () {
        return this.offsetWidth
      }
    },
    beforeRouteLeave (to, from ,next) {
      $(window).off('scroll.btn-top')
      $(window).off('resize.btn-top')
      $(window).off('scroll.sticky-onscroll resize.sticky-onscroll')
      next()
    },
    mounted () {
      $(window).on('scroll.btn-top', this.handleScroll)
      $(window).on('resize.btn-top', this.handleResize)
      this.offsetWidth = $('.deal_body').outerWidth() +  $('.deal_body').offset().left
      $(document).ready(function() {
        // Custom
        var stickyToggle = function(sticky, stickyWrapper, scrollElement) {
          var stickyHeight = sticky.outerHeight();
          var stickyTop = stickyWrapper.offset().top;
          if (scrollElement.scrollTop() >= stickyTop){
            stickyWrapper.height(stickyHeight);
            sticky.addClass("is-sticky");
            $('.dynamic-search').addClass("col-lg-4");
            $('.dynamic-nav').removeClass("zIndex");
            $('.dynamic-nav').addClass('nav-bottom');
          }
          else{
            sticky.removeClass("is-sticky");
            $('.dynamic-search').removeClass("col-lg-4");
            $('.dynamic-nav').addClass("zIndex");
            $('.dynamic-nav').removeClass('nav-bottom');
            stickyWrapper.height(0);
          }
        };
        // Find all data-toggle="sticky-onscroll" elements
        $('[data-toggle="sticky-onscroll"]').each(function() {
          var sticky = $(this);
          var stickyWrapper = $('<div>').addClass('sticky-wrapper'); // insert hidden element to maintain actual top offset on page
          sticky.before(stickyWrapper);
          sticky.addClass('sticky');

          // Scroll & resize events
          $(window).on('scroll.sticky-onscroll resize.sticky-onscroll', function() {
            stickyToggle(sticky, stickyWrapper, $(this));
          });

          // On page load
          stickyToggle(sticky, stickyWrapper, $(window));
        });
      });
    }
  }
</script>



